// fonts
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap');

// color vars
$color-black: #333;
$grey-1: #666;
$grey-2: #999;
$light-blue: #0052d6;
$light-blue-1: #0098e9;
$dark-blue: #002856;
$dark-blue-2: #013575;
$blue-3: #596a85;
$blue-4: #4a629e;
$blue-5: #e9f0f5;
$white: #fff;
$dark-orange: #db5d23;
$dark-orange-2: rgba(219, 93, 35, 0.8);
$dark-orange-3: #ff5900;
$yellow-1: #f8a306;

*:not(input), *:not(textarea) {
  // user-select: none;
  @media only screen and (max-width: 991px) {
    user-select: auto;
  }
}

*:not(.email-case, .task-head-name)::first-letter {
  text-transform: uppercase;
}
// .form-control:not([type="email"], [type="password"]){
//   text-transform: unset;
// }
.email-case::first-letter, .email-case *::first-letter{
  text-transform: unset;
}

// material icons
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('./../fonts/MaterialIcons/MaterialIcons-Regular.eot');
  src: local('Material Icons'), local('MaterialIcons-Regular'),
    url('./../fonts/MaterialIcons/MaterialIcons-Regular.woff2') format('woff2'),
    url('./../fonts/MaterialIcons/MaterialIcons-Regular.woff') format('woff'),
    url('./../fonts/MaterialIcons/MaterialIcons-Regular.ttf') format('truetype');
}
.mui-icon {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* Preferred icon size */
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}

// transition effect
%transitionSlide {
  transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
  transition-property: top, bottom, width;
  transition-duration: 0.2s, 0.2s, 0.35s;
  transition-timing-function: linear, linear, ease;
}

// scale animation
@keyframes ScaleAnimation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

/************ Common css starts ************/
html,
body {
  font-family: 'Roboto', sans-serif;
  color: $color-black;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  background-color: #ebeff4;
}

body {
  overflow-x: hidden;
}

.pointer-none{
  pointer-events: none;
}
.c-pointer{
  cursor: pointer;
}

// button css
.btn {
  padding: 0.375rem 1rem;
  border-radius: 3px;
  &-xs {
    padding: 0.25rem 1rem;
    &.h-auto {
      height: auto;
    }
  }
  &:focus {
    box-shadow: none;
  }
  &-blue {
    background-color: $dark-blue;
    border-color: $dark-blue;
    &:hover, &:disabled {
      background-color: $dark-blue-2;
      border-color: $dark-blue-2;
    }
    &:not(:disabled) {
      &:not(.disabled):active {
        box-shadow: none;
        &:focus {
          background-color: $dark-blue-2;
          border-color: $dark-blue-2;
          box-shadow: none;
        }
      }
    }
  }
  &-blue-light {
    background-color: transparent;
    border-color: $light-blue-1;
    &.active {
      background-color: $light-blue-1 !important;
      color: #fff !important;
      border-color: $light-blue-1 !important;
    }
  }
  &-outlined {
    &.btn-blue {
      background-color: transparent;
      color: $dark-blue;
      &:hover {
        border-color: $dark-blue-2;
        color: $dark-blue-2;
      }
      &.active {
        border-color: $dark-blue-2;
        background-color: $dark-blue-2;
        color: #fff;
        &:not(:disabled):not(.disabled):active:focus {
          border-color: $dark-blue-2;
          background-color: $dark-blue-2;
          color: #fff;
        }
      }
      &:not(:disabled) {
        &:not(.disabled):active {
          box-shadow: none;
          &:focus {
            border-color: $dark-blue-2;
            color: $dark-blue-2;
            background-color: transparent;
            box-shadow: none;
          }
        }
      }
    }
    &.btn-blue-light {
      background-color: transparent;
      color: $light-blue-1;
      &:hover {
        color: #fff;
        background-color: $light-blue-1;
        border-color: $light-blue-1 !important;
      }
      &:not(:disabled) {
        &:not(.disabled):active {
          box-shadow: none;
          &:focus {
            color: #fff;
            background-color: $light-blue-1;
            box-shadow: none;
          }
        }
      }
    }
  }
  &-lg {
    font-size: 1em;
    height: 48px;
  }
  &-sm {
    font-size: 0.875em;
    height: 42px;
  }
  &-head {
    font-size: 0.938em;
    height: 38px;
    &.btn-icn {
      width: 38px;
      padding-left: 0px;
      padding-right: 0px;
      text-align: center;
    }
  }
  &-default {
    background-color: #ebebeb;
    border-color: #ccc;
    color: #666;
  }
  &-orange {
    background-color: $dark-orange;
    border-color: $dark-orange;
    &:hover {
      background-color: $dark-orange-2;
      border-color: $dark-orange-2;
    }
    &:not(:disabled) {
      &:not(.disabled):active {
        box-shadow: none;
        &:focus {
          background-color: $dark-orange-2;
          border-color: $dark-orange-2;
          box-shadow: none;
        }
      }
    }
    &-lg {
      height: 56px;
      font-size: 18px;
      width: 290px;
      @media only screen and (max-width: 767px) {
        height: 40px;
        font-size: 16px;
      }
    }
  }
  &-no-hover {
    &:hover {
      text-decoration: none;
    }
  }
  &-grid {
    font-size: 15px;
    padding-top: 4px;
    padding-bottom: 4px;
    height: auto;
  }
  &-sort {
    border: 1px solid rgba(0, 40, 86, 0.2);
    border-radius: 50%;
    & + .btn-sort {
      margin-left: 5px;
    }
    &.w-icn {
      border-radius: 50%;
      padding: 2px;
      width: 30px;
      height: 30px;
      img {
        max-width: 100%;
      }
    }
    &.wt-icn {
      border-radius: 20px;
      padding: 0px 6px;
      height: 30px;
      font-size: 13px;
      align-items: center;
      display: inline-flex;
      span {
        margin-right: 2px;
        margin-left: 3px;
        color: $dark-blue;
        font-weight: 500;
      }
    }
    svg {
      width: 17px;
      height: 17px;
      fill: $dark-blue;
    }
  }
}

// search box
.search {
  &-big {
    width: 400px !important;
  }
}

// custom container
.container-custom {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}

// title sizes css
.title {
  &-blue {
    font-size: 1.25em;
    color: $dark-blue;
  }
  &-small {
    font-size: 1em;
    color: $blue-3;
  }
  &-med {
    font-size: 1.375em;
    color: #474747;
  }
  &-big {
    font-size: 1.626em;
    font-weight: 600;
  }
}

// checkbox css
.custom-checkbox {
  font-size: 0.875em;
  color: $grey-1;
  margin-right: 2em;
  .custom-control-label {
    cursor: pointer;
    &::before,
    &::after {
      height: 1em;
      width: 1em;
      top: 0.2rem;
    }
  }
}

// radio button css
.custom-radio {
  &-sm {
    .custom-control-label {
      font-size: 0.875em;
      &::before,
      &::after {
        top: 0.15rem;
      }
    }
  }
}

// custom select
.c-select {
  position: relative;
  border: 1px solid #c8c8c8;
  border-radius: 2px;
  background-color: #fff;
  select {
    border-radius: 3px;
    padding-right: 30px;
    -webkit-appearance: none;
    border: none;
    text-transform: capitalize;
    &.form-control {
      height: 40px;
      &-xs {
        height: 34px;
        font-size: 0.875em;
      }
      &-sm {
        height: 38px;
      }
      &-lg {
        height: 46px;
      }
    }
  }
  &:after {
    content: '';
    right: 10px;
    position: absolute;
    width: 0;
    height: 0;
    top: 10px;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid rgba(0, 0, 0, 0.54);
    font-size: 0;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }
  &.w-border {
    border-color: transparent;
  }
}

// font sizes
.font {
  &-12 {
    font-size: 0.75em;
  }
  &-14 {
    font-size: 0.875em !important;
  }
  &-15 {
    font-size: 0.938em;
  }
  &-16 {
    font-size: 1em;
  }
  &-18 {
    font-size: 1.125em;
  }
  &-20 {
    font-size: 1.25em;
  }
  &-22 {
    font-size: 1.375em;
  }
  &-24 {
    font-size: 1.5em;
  }
  &-26 {
    font-size: 1.626em;
  }
}

// font weights
.font {
  &-600 {
    font-weight: 600;
  }
  &-500 {
    font-weight: 500;
  }
  &-400 {
    font-weight: 400;
  }
  &-300 {
    font-weight: 300;
  }
}

// width classes
.w {
  &-140 {
    min-width: 140px;
  }
  &-80 {
    min-width: 80px;
  }
  &-18 {
    width: 18px !important;
  }
}

// input with icon
.icon-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  input {
    padding-right: 50px;
  }
  .form-control-icn {
    position: absolute;
    top: 0;
    right: 0px;
    width: 48px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    z-index: 8;
    color: $grey-1;
    svg {
      font-size: 1.25em;
    }
    &-sm {
      width: 42px;
      height: 42px;
      line-height: 42px;
    }
  }
  &.blue-wrapper {
    .form-control-icn {
      color: $dark-blue;
    }
  }
}

// input normal
.form-control {
  border-radius: 2px;
  border-color: #c8c8c8;
  color: #474747;
  padding-left: 10px;
  padding-right: 10px;
  height: 42px;
  resize: none;
  font-size: 1em;
  &[type=number]::-webkit-inner-spin-button, 
  &[type=number]::-webkit-outer-spin-button{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  &:focus {
    border-color: $light-blue;
    box-shadow: none;
  }
  &.is-invalid {
    background-image: none;
  }
  &-lg {
    font-size: 1em;
    height: 48px;
  }
  &-sm {
    font-size: 0.875em;
  }
  &-head {
    height: 38px;
    font-size: 0.875em;
    + .form-control-icn {
      width: 38px;
      height: 38px;
      line-height: 38px;
    }
  }
}

// dropdown action
.dropdown {
  &-action {
    .dropdown-toggle {
      svg {
        height: 21px;
        width: 21px;
      }
      &::after {
        display: none;
      }
    }
    .dropdown-menu {
      .dropdown-item {
        color: #848f99;
        font-size: 0.75em;
        padding: 5px 15px;
      }
    }
  }
  &-campaign {
    width: 100%;
    .dropdown {
      &-toggle {
        width: 100%;
        text-align: left;
        border: 1px solid #c8c8c8;
        border-radius: 2px;
        color: #474747;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        &:after {
          position: absolute;
          right: 10px;
          top: 16px;
        }
      }
      &-item {
        padding: 0.25rem 35px 0.25rem 1rem;
        position: relative;
        cursor: pointer;
        white-space: pre-wrap;
        &:not(:last-child) {
          border-bottom: 1px solid #f0f0f0;
        }
        &.active,
        &:active {
          &:hover {
            .btn {
              color: #fff;
            }
          }
          .btn {
            color: #fff;
          }
        }
        .btn {
          position: absolute;
          right: 5px;
          top: 2px;
          padding: 0px;
          z-index: 2;
          display: none;
          color: $grey-2;
          &:hover {
            color: $dark-blue;
          }
          svg {
            width: 18px;
            height: 18px;
          }
        }
        &:hover {
          .btn {
            display: block;
          }
        }
      }
      &-menu {
        width: 100%;
        font-size: 15px;
        max-height: 200px;
        overflow: auto;
        padding: 0px;
      }
    }
  }
}

// custom file browse
.custom-browse {
  cursor: pointer;
  input[type='file'] {
    left: -80px;
    cursor: pointer;
  }
  label {
    position: absolute;
    top: 0;
    left: 0;
  }
}

// margin css
.mt-10 {
  margin-top: 10px;
}

// form css
.form {
  &-sm {
    .form-label {
      font-size: 0.938em;
    }
  }
  &-admin {
    .form-label {
      color: $grey-1;
    }
  }
}
.form-label {
  margin-bottom: 2px;
}
.form-val {
  margin-bottom: 0px;
  color: #000;
}

// custom select css
div {
  &.react-select-wrapper {
    span[class*='-indicatorSeparator'] {
      background-color: initial;
    }
    div[class*='-indicatorContainer'] {
      svg {
        opacity: 0;
      }
      &:after {
        content: '';
        right: 10px;
        position: absolute;
        width: 0;
        height: 0;
        top: 50%;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        font-size: 0;
        border-top: 5px solid rgba(0, 0, 0, 0.54);
        transform: translateY(-50%);
      }
    }
    div[class*='-control'] {
      cursor: pointer;
      box-shadow: none !important;
