// color css
.color {
  &-grey-1 {
    color: $grey-1;
  }
  &-grey-2 {
    color: $grey-2;
  }
  &-black {
    color: $color-black;
  }
  &-dark-blue {
    color: $dark-blue;
  }
  &-light-blue-1 {
    color: $light-blue-1 !important;
  }
  &-blue-3 {
    color: $blue-3;
  }
  &-blue-4 {
    color: $blue-4;
  }
  &-yellow-1 {
    color: $yellow-1;
  }
