  }
}

// status colors
.status-mark {
  height: 6px;
  width: 6px;
  border-radius: 50%;
  margin: 0 8px;
  display: inline-block;
  &-grey {
    background: $grey-2;
  }
  &-blue {
    background: $light-blue-1;
  }
  &-orange {
    background: #ff5900;
  }
  &-green {
    background: #009912;
  }
  &-text {
    color: #848f99;
  }
  &-9 {
    height: 9px;
    width: 9px;
  }
}

// datepicker
.react-bootstrap-daterangepicker-container {
  width: 100%;
}

.react-date-picker {
  &__wrapper {
    font-size: 0.875em;
    height: 33px;
    border-color: #c8c8c8;
    color: #474747;
    padding-left: 10px;
    width: 135px;
  }
  &__calendar {
    z-index: 99;
    font-size: 12px;
    width: 270px;
    margin-bottom: 5px;
    margin-top: 5px;
    .react-calendar {
      border-color: #ddd;
      border-radius: 4px;
      &__navigation {
        height: 30px;
        margin-bottom: 0px;
        &__prev2-button,
        &__next2-button {
          display: none;
        }
        &__prev-button,
        &__next-button {
          font-size: 0;
          &:hover,
          &:focus {
            background-color: transparent !important;
            outline: none;
          }
          &:after {
            content: '';
            color: #fff;
            border: solid black;
            border-width: 0 2px 2px 0;
            border-radius: 0;
            display: inline-block;
            padding: 3px;
            height: 8px;
            width: 8px;
            position: absolute;
            top: 10px;
          }
          &[disabled] {
            opacity: 0;
          }
        }
        &__next-button {
          &:after {
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            right: 18px;
          }
        }
        &__prev-button {
          &:after {
            transform: rotate(135deg);
            -webkit-transform: rotate(135deg);
            left: 18px;
          }
        }
        &__label {
          font-size: 12px;
          font-weight: bold;
        }
      }
      &__tile {
        color: #333 !important;
        border-radius: 4px;
        &:focus {
          outline: none;
        }
        &[disabled] {
          cursor: not-allowed;
          text-decoration: line-through;
          background-color: transparent;
          color: #999 !important;
        }
        &--active {
          background-color: #357ebd;
          color: #fff !important;
        }
      }
      &__month-view {
        &__days__day--weekend {
          color: #333;
        }
        &__days__day--neighboringMonth {
          color: #999 !important;
        }
        &__weekdays__weekday {
          text-transform: capitalize;
          font-size: 12px;
          abbr {
            text-decoration: none;
          }
        }
      }
    }
  }
  &__inputGroup__input {
    color: #474747;
    // pointer-events: none;
    &::selection {
      background-color: transparent;
    }
    &:focus {
      // pointer-events: none;
      outline: none;
      caret-color: transparent;
    }
  }
  &__calendar-button {
    &:focus {
      outline: none;
    }
    svg {
      width: 20px;
      height: 20px;
      color: #666;
    }
  }
  &__inputGroup__divider {
    pointer-events: none;
  }
  // width: 33px;
  // height: 33px;
  // line-height: 30px;
}

// bootstrap modal
.modal {
  padding-left: 240px !important;
  background-color: rgba(0, 0, 0, 0.4);
  &-header {
    align-items: center;
    .close {
      font-size: 35px;
      -webkit-text-stroke: 2px #fff;
      color: $blue-3;
      opacity: 1;
      &:focus {
        outline: none;
      }
    }
    &-none {
      padding: 0px;
      border: none;
      .close {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 9;
      }
    }
  }
  &-title {
    font-size: 1.375em;
    color: $dark-blue;
  }
  &-footer {
    border-color: transparent;
    padding-top: 0px;
    justify-content: flex-start;
    & > :not(:last-child) {
      margin-right: 0.5rem;
    }
    &-inner {
      margin: 0 -15px;
      border-top: 1px solid #ccc;
      padding-top: 1rem;
    }
  }
  &-small {
    .modal-dialog {
      max-width: 345px;
    }
  }
  &-med {
    .modal-dialog {
      max-width: 465px;
    }
  }
  &-med1 {
    .modal-dialog {
      max-width: 600px;
    }
  }
  &-delete {
    background-color: rgba(0, 0, 0, 0.4);
    .modal-content {
      padding-top: 20px;
      padding-bottom: 10px;
    }
    .modal-footer {
      padding-top: 15px;
    }
    p {
      font-size: 20px;
    }
    svg {
      font-size: 3em;
    }
    .modal-footer {
      justify-content: center;
    }
  }
  &-786 {
    .modal-dialog {
      max-width: 786px;
    }
  }
  &-big {
    .modal-dialog {
      max-width: 970px;
    }
  }
  &-400 {
    .modal-dialog {
      max-width: 400px;
      @media only screen and (max-width: 991px) {
        margin: 0 auto;
      }
      @media only screen and (max-width: 767px) {
        max-width: 90%;
      }
    }
  }
  &-cal-task {
    .modal-footer {
      border-top: 1px solid #dee2e6;
      padding-top: 1rem;
    }
    .view-task-actions {
      button {
        padding: 0 5px;
        font-size: unset;
        float: none;
      }
    }
  }
  &-preview {
    padding-left: 0px !important;
    overflow: hidden !important;
    .modal-dialog {
      max-width: 100%;
      margin: 0px;
      .modal-header {
        padding-top: 10px;
        padding-bottom: 10px;
      }
      .modal-body {
        padding: 0px;
        height: calc(100vh - 60px);
        img{
          padding: 5px;
          margin: 0 auto;
          display: block;
          max-width: 100%;
        }
      }
      .modal-content {
        border-radius: 0px;
        border: none;
      }
    }
  }
  &-backdrop {
    & + .modal {
      background-color: transparent;
    }
  }
}

// step process
.step-progress {
  position: relative;
  .multi-step {
    & > .container {
      max-width: 100%;
      padding: 0px;
    }
    .progtrckr {
      background-color: #fff;
      list-style: none;
      margin: 0px;
      padding: 10px 0px;
      text-align: center;
      box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 2px 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        display: inline-block;
        position: relative;
        padding-right: 50px;
        display: flex;
        align-items: center;
        &:after {
          content: '';
          // background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, #d0d0d0), color-stop(0%, rgba(255, 255, 255, 0)));
          // background-image: -webkit-linear-gradient(left, #d0d0d0 50%, rgba(255, 255, 255, 0) 0%);
          // background-image: linear-gradient(to right, #d0d0d0 50%, rgba(255, 255, 255, 0) 0%);
          // background-position: top;
          // background-size: 6px 1px;
          // background-repeat: repeat-x;
          // width: 30px;
          // height: 1px;
          // position: absolute;
          // top: 50%;
          // right: 8px;
          position: absolute;
          width: 25px;
          border-bottom: 1px dashed #ebebeb;
          border-width: 2px;
          top: 46%;
          right: 12px;
        }
        span {
          pointer-events: none;
        }
        em {
          color: transparent;
          height: 18px;
          width: 18px;
          background-color: #ebebeb;
          border-radius: 50%;
          display: inline-block;
          margin-right: 8px;
        }
        &:last-child {
          padding-right: 0px;
          &:after {
            display: none;
          }
        }
        &.progtrckr-todo {
          pointer-events: none;
        }
        &.progtrckr-doing {
          color: $light-blue;
          pointer-events: none;
          em {
            background-color: $light-blue;
          }
        }
        &.progtrckr-done {
          color: $dark-blue;
          em {
            background-color: $dark-blue;
          }
          &:after {
            border-color: $dark-blue;
          }
        }
      }
    }
    // for admin
    &-admin {
      .progtrckr {
        position: absolute;
        top: 0px;
        left: 50%;
        transform: translateX(-50%);
        background-color: transparent;
        box-shadow: none;
        height: 67px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.875em;
        // width: 60%;
        li {
          padding-right: 40px;
          em {
            color: $grey-1;
            font-weight: 600;
            height: 24px;
            width: 24px;
            font-size: 14px;
            font-style: normal;
            background-color: transparent;
            border: 1px solid $grey-2;
            padding-top: 1px;
            text-align: center;
          }
          &:after {
            right: 8px;
          }
          &.progtrckr-doing {
            em {
              color: #fff;
              border-color: $light-blue;
            }
          }
          &.progtrckr-done {
            em {
              color: #fff;
              border-color: $dark-blue;
            }
          }
        }
      }
    }
  }
}

