// material switch UI
.material-switch {
  & > input[type='checkbox'] {
    display: none;
    &:checked + label::before {
      background: #9ca8bb;
    }
    &:checked + label::after {
      background: $dark-blue;
      right: -4px;
    }
  }
  & > label {
    cursor: pointer;
    position: relative;
    padding-right: 45px;
    color: $blue-3;
    &::before {
      background: #c7c7c7;
      border-radius: 8px;
      content: '';
      height: 14px;
      position: absolute;
      transition: all 0.4s ease-in-out;
      width: 32px;
      right: 0px;
      top: 2px;
    }
    &::after {
      background: white;
      border-radius: 16px;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
      content: '';
      right: 16px;
      margin-top: -8px;
      position: absolute;
      top: 7px;
      transition: all 0.3s ease-in-out;
      height: 20px;
      width: 20px;
    }
  }
}

