// activity
.activity {
  &-header {
    padding-top: 20px;
    h5 {
      display: inline-block;
      color: $grey-2;
      font-size: 1.125em;
      width: 151px;
      border: 1px solid #dee2ee;
      border-radius: 100px;
      padding: 10px 0px;
      font-weight: 600;
      margin: 0px;
      @media only screen and (max-width: 767px) {
        width: 100%;
      }
    }
  }
  &-body {
    position: relative;
    min-height: calc(100vh - 187px);
    padding-top: 40px;
    &::after {
      content: '';
      height: 100%;
      width: 1px;
      background-color: #dee2ee;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 0px;
    }
  }
  &-single {
    padding-bottom: 15px;
    margin-left: 0px;
    margin-right: 0px;
    & > .col {
      padding: 0 25px;
      @media only screen and (max-width: 767px) {
        padding: 0 10px;
      }
    }
    &-content {
      border: 1px solid #ddd;
      border-radius: 2px;
      max-width: 350px;
      padding: 10px 15px;
      position: relative;
      @media only screen and (max-width: 767px) {
        padding: 5px 10px;
      }
      &:before {
        content: '';
        position: absolute;
        height: 12px;
        width: 12px;
        background-color: #30a8ee;
        left: -32px;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        z-index: 2;
        @media only screen and (max-width: 767px) {
          left: -17px;
        }
      }
    }
    .date {
      font-size: 0.875em;
      text-align: right;
      margin: 0px;
    }
    .title {
      font-size: 1em;
      color: #30a8ee;
    }
    .desc {
      font-size: 0.938em;
      font-weight: 400;
      margin-bottom: 12px;
    }
    .file {
      &-title {
        margin-bottom: 2px;
        font-size: 0.875em;
        line-height: 0.875em;
        font-weight: 500;
      }
      &-text {
        margin-bottom: 2px;
        font-size: 0.875em;
        line-height: 0.875em;
        font-style: italic;
      }
      &-auth {
        font-style: italic;
        margin: 0px;
        font-size: 0.75em;
      }
    }
  }
  &-row {
    &:nth-child(even) {
      .activity {
        &-single {
          flex-flow: row-reverse;
          .date {
            text-align: left;
          }
          &-content {
            float: right;
            &:before {
              left: auto;
              right: -32px;
              @media only screen and (max-width: 767px) {
                right: -17px;
              }
            }
          }
        }
      }
    }
  }
  &-filter {
    .dropdown-toggle {
      &:after {
        display: none;
      }
    }
    &-menu{
      width: 380px;
      padding: 10px;
      box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
      top: 38px !important;
      transform: none !important;
      right: 5px !important;
      left: auto !important;
      .react-datepicker-popper{
        right: 0;
        left: auto !important;
      }
    }
  }
}

