    &-month-view,
    &-time-view {
      margin: 25px auto 0px auto;
      width: calc(100% - 50px);
      padding: 0px;
    }
    &-row-content {
      border-left: 1px solid rgba(221, 221, 221, 0.5);
      border-right: 1px solid rgba(221, 221, 221, 0.5);
    }
    &-event {
      text-align: left;
      font-size: 0.75em;
      font-weight: 500;
      padding: 3px 5px;
      width: calc(100% - 30px);
      float: right;
      margin-bottom: 2px;
      border-radius: 2px;
      box-shadow: none;
      &>div{
        font-size: 12px;
        &.rbc-event-label{
          display: none;
        }
      }
      &+.rbc-event{
        border-left: 1px solid #fff;
      }
      &:focus {
        outline: none;
      }
      &:before {
        content: '';
        position: absolute;
        height: 9px;
        width: 9px;
        left: -20px;
        top: 5px;
        border-radius: 50%;
        background-color: $grey-1;
      }
      &.event-blue {
        background-color: $light-blue-1;
        color: #fff;
        &:before {
          background-color: $light-blue-1;
        }
      }
      &.event-orange {
        background-color: $dark-orange-3;
        color: #fff;
        &:before {
          background-color: $dark-orange-3;
        }
      }
      &.event-green {
        background-color: #3e8a30;
        color: #fff;
        &:before {
          background-color:#3e8a30;
        }
      }
    }
    &-btn-group {
      button {
        border: 1px solid #ccc;
        border-radius: 0px;
        margin: 0px;
        height: 38px;
        color: $color-black;
        font-size: 13px;
        border-radius: 4px;
        &.rbc-active {
          background-color: $dark-blue;
          color: #fff;
          border-color: $dark-blue;
        }
      }
      &.cal-view {
        button {
          border-radius: 0px;
          &:first-child {
            border-radius: 4px 0px 0px 4px;
          }
          &:last-child {
            border-radius: 0px 4px 4px 0px;
          }
          & + button {
            border-left: none;
          }
        }
      }
      .icon-wrapper {
        input {
          border-radius: 4px;
        }
      }
    }
    &-time-view{
      .rbc-allday-cell{
        display: none;
      }
      .rbc-time-content{
        max-height: calc(100vh - 280px);
        .rbc-event{
          border-radius: 10px;
        }
      }
    }
    &-row-segment{
      overflow: hidden;
    }
  }
  .cal-task-slide{
    position: absolute !important;
    top: 0;
    right: 0;
    max-height: calc(100vh - 125px) !important;
    z-index: 1040;
  }
}

@for $i from 1 through 500{
