.dependency-select {
  div {
    &.react-select-multi {
      div[class*='-control'] {
        border: none;
        & > div:first-child {
          & > div:last-child {
            border: 1px solid #ccc !important;
            width: 100%;
            padding: 5px;
            position: relative;
            border-radius: 2px;
            &:after {
              content: '';
              right: 10px;
              position: absolute;
              width: 0;
              height: 0;
              top: 10px;
              border-left: 5px solid transparent;
              border-right: 5px solid transparent;
              border-top: 5px solid rgba(0, 0, 0, 0.54);
              font-size: 0;
              top: 50%;
              -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
              pointer-events: none;
            }
            input {
              // color: transparent;
              // &:focus{
              //     &+div{
              //         &:before{
              //             display: none;
              //         }
              //     }
              // }
              & + div {
                visibility: visible !important;
                height: auto !important;
                overflow: unset !important;
                top: 5px !important;
                left: 6px !important;
                color: transparent;
                &:empty {
                  &:before {
                    content: 'Select Task';
                    color: grey;
                  }
                }
              }
            }
          }
        }
        div[class*='-placeholder'] {
          padding-left: 5px;
        }
        & > div {
          padding: 0px;
        }
        div[class*='-multiValue'] {
          width: 100%;
          background-color: #f2f2f2;
          border-radius: 5px;
          & + div[class*='-Input'] {
            margin-top: 10px;
          }
