            &:first-child {
              flex: 1;
              overflow: auto;
              white-space: unset;
              text-overflow: unset;
            }
            &:last-child {
              padding-right: 9px;
              padding-left: 9px;
            }
          }
        }
        & > div:last-child {
          display: none;
        }
      }
      div[class*='-menu']{
        width: calc(100% - 90px);
      }
    }
  }
}

