@media only screen and (max-width: 991px) {
  body {
    font-size: 0.875em;
  }
  .hide-991 {
    display: none;
  }

  .nav-head {
    align-items: center !important;
    .navbar-toggler {
      border-color: transparent;
      outline: none;
      .navbar-toggler-icon {
        background-image: none;
        &:after {
          font-family: 'Material Icons';
          color: #002856;
          content: '\e5d2';
          font-size: 30px;
        }
      }
    }
