  // content area
  .contentArea {
    &.collapsed {
      .nav-admin {
        .close-icn {
          display: block;
        }
      }
    }
  }
  .modal {
    padding-left: 0px !important;
    &-view-page {
      .modal-header {
        padding: 20px;
        border: none;
        position: relative;
        .close {
          position: absolute;
          right: 15px;
          top: 15px;
          padding: 0px;
          height: 40px;
          width: 40px;
          line-height: 40px;
        }
      }
      .modal-body {
        padding: 0px;
