      }
    }
  }

  .search-big {
    width: auto !important;
  }

  // sidebar
  .sidebar {
    width: 0;
    .sidebar-header {
      min-height: auto;
      height: 47px;
      .nav-link {
        img {
          width: 0;
        }
      }
    }
    .close-sidebar {
      display: block;
    }

    &.collapsed {
      width: 100%;
      display: block;
      .nav-link {
        margin: 0px;
      }
      &:hover {
        width: 100%;
        .sidebar-header {
          .nav-link {
            padding: 0px;
            img {
              width: 105px;
            }
          }
        }
      }
      .side-label {
        opacity: 1;
      }
      .sidebar-header {
        justify-content: flex-start;
        .nav-link {
          img {
            width: 156px;
          }
        }
      }
    }
  }

  // content area
  .contentArea {
    width: 100%;
    &.collapsed {
      width: 100%;
      max-height: 100vh;
      overflow: hidden;
    }
  }

  .nav-admin {
    .navbar-brand {
      display: block;
    }
    .logo-mobile {
      display: block;
      width: 105px;
    }
    .navbar-collapse {
      flex-basis: auto;
      display: flex;
      justify-content: flex-end;
      flex-flow: unset;
      .user-dropdown {
        .dropdown-menu {
          position: absolute;
        }
        button {
          padding-right: 0px;
        }
        .user-dropdown-name {
          display: none;
        }
        .dropdown-toggle::after {
          display: none;
        }
      }
      .navbar-nav {
        display: flex;
        flex-direction: unset;
        margin: 0px !important;
      }
    }
  }

  .page-head-row {
    padding: 0px;
  }
  .trans {
    &-table:not(.trans-table-normal) {
      width: 100%;
      td {
        width: 50%;
      }
    }
    &-combo {
      display: flex;
      align-items: center;
      margin-top: 10px;
      .trans-img {
        width: 32px;
        height: 32px;
      }
    }
    &-info {
      h5 {
        font-size: 13px;
      }
      p {
        font-size: 11px;
      }
    }
    &-tabs {
      .nav-item {
        padding: 0 7px;
      }
    }
  }

  .task-single.open {
    width: 50vw;
  }

  .task {
    &-title-row {
      margin-top: 0px;
    }
    &-schedule {
      order: 3;
    }
    &-head-name {
      order: 2;
    }
  }

  .task-row .task-action {
    opacity: 1;
  }

  .task-accordion {
    padding: 8px 42px;
    span:after {
      left: 13px;
    }
  }

  .trans-tab-content .folder-list-wrapper {
      height: calc(100vh - 275px);
  }

  .task-mobile-modal {
    .modal-dialog {
      max-width: 100%;
      margin: 0px;
      .modal-content {
        border-radius: 0;
        border: none;
      }
    }
    .task-date input {
      border: none;
      background: transparent;
      padding-right: 0px;
      width: 85px;
    }
    .task-date {
      position: relative;
      border: 1px solid #c8c8c8;
      border-radius: 40px;
      height: 43px;
      padding-left: 15px;
      .form-control-icn {
        position: relative;
        top: -2px !important;
        padding-right: 5px;
      }
    }
    .task-schedule {
      flex: 1;
      .dependency-combo {
        position: absolute;
        top: -91px !important;
        right: 30px;
        .dep-btn{
          padding: 0px;
        }
        &.col {
          padding-right: 0px !important;
        }
        .col {
          padding-left: 0px !important;
          padding-right: 0px !important;
        }
      }
    }
    .add-task-modal {
      .dependency-combo {
        top: -91px;
      }
    }
    .ck-toolbar {
      display: none !important;
    }
    .ck.ck-content {
      border-top: 1px solid #c4c4c4 !important;
    }
  }
}

